
























































































import Vue from "vue";
import Component from "vue-class-component";
import LoginFirst from "@/x-vue/components/user/LoginFirst.vue";
import { ApiService } from "@/x-vue/services/api.service";
import { RequestData, UserModel } from "@/x-vue/interfaces/interfaces";

@Component({
  components: {
    LoginFirst,
  },
})
export default class Profile extends Vue {
  user: UserModel = {} as UserModel;
  api: ApiService = ApiService.instance;

  fetching = true;

  async mounted(): Promise<void> {
    if (this.api._user.notLoggedIn) {
      this.fetching = false;
      return;
    }

    const idx = this.$route.params.idx;

    try {
      if (!idx) {
        this.user = await this.api.userProfile();
      } else {
        this.user = await this.api.otherUserProfile(idx);
      }
      this.fetching = false;
      // console.log(this.user);
    } catch (e) {
      this.$emit("error", e);
      this.fetching = false;
    }
  }

  get isMe(): boolean {
    return this.user.idx == this.api._user.idx;
  }

  onSubmit($event: Event): void {
    let form: RequestData = {
      idx: this.user.idx,
      email: this.user.email,
      firebaseUid: this.user.firebaseUid,
      name: this.user.name,
      nickname: this.user.nickname,
      phoneNo: this.user.phoneNo,
      gender: this.user.gender,
      birthdate: this.user.birthdate,
      countryCode: this.user.countryCode,
      province: this.user.province,
      city: this.user.city,
      address: this.user.address,
      zipcode: this.user.zipcode,
    };

    this.$emit("submit", $event, form);
  }
}
